*, *::before, *::after{
   margin: 0;
   border: 0;
   outline: 0;
   list-style: none;
   text-decoration: none;
   box-sizing: border-box;
}

:root{
   --primary-hue: 210;
   --gray-hue: 240;
   --color-primary: hsl(var(--primary-hue),100%, 50%);
   --color-primary-variant: hsl(var(--primary-hue),100%,65%);
   --color-secondary: #ffb116;
   --color-gray-100: hsl(var(--gray-hue), 47%, 94%);
   --color-gray-200: hsl(var(--gray-hue), 26%, 75%);
   --color-gray-300: hsl(var(--gray-hue), 24%, 64%);
   --color-gray-400: hsl(var(--gray-hue), 16%, 41%);
   --color-gray-500: hsl(var(--gray-hue), 44%, 25%);
   --color-gray-600: hsl(var(--gray-hue), 48%, 15%);

   --container-width-lg: 80%;
   --container-width-md: 90%;
   
   --transition: all 500ms ease;
}

/* GENERAL STILES */

body {
   font-family: 'Montserrat', sans-serif;
   color: var(--color-gray-200);
   line-height: 1.7;
   overflow-x: hidden;
   background: var(--color-gray-600)  url('./images/bg_texture.png');
}

.container{
   width: var(--container-width-lg);
   max-width: 1920px;
   margin-inline: auto;
}

h1,h2,h3,h4,h5{
   line-height: 1.2;
   color:  var(--color-gray-100);
}

h1{
   font-size: 3rem;
}

h2{
   font-size: 2rem;
}

a{
   color: var(--color-gray-100);
}

img{
   display: block;
   object-fit: cover;
   width: 100%;
}



/* ----------- button styles----------- */


.btn{
   color: var(--color-gray-100);
   width: fit-content;
   margin: 0 auto;
   padding: 0.9rem 2rem;
   background: var(--color-primary);
   border-radius: 1.5rem;
   transition: var(--transition);
}

.btn:hover{
   background: var(--color-secondary);
   color: var(--color-gray-600);
}

.btn.lg{
   padding: 1rem 2.3rem;
   border-radius: 2rem;
   font-size: 1.1rem;
}

.btn.sm{
   padding: 0.5rem 1.2rem;
   font-size: 0.8rem;
}

.btn.sm:hover{
   color: var(--color-gray-500);
}

/* -----------styles for the section head componenet------- */


section{
   margin-top: 10rem;
}

.section__head{
   display: flex;
   align-items: center;
   gap: 1rem;
}

.section__head span{
   background: var(--color-gray-500);
   padding: 0.3rem 0.6rem 0;
   border-radius: 0.5rem;
   color: var(--color-secondary);
   font-size: 2rem;
}


/* ----------styles for card------------- */

.card{
   background: var(--color-gray-500);
   border: 2px solid transparent;
   text-align: center;
   padding: 2rem 2rem;
   border-radius: 0 3rem 0 3rem;
   transition: var(--transition);
}

.card:hover{
   background: transparent;
   border-color: var(--color-gray-400);
   cursor: default;
}

.card span{
   width: 3rem;
   height: 3rem;
   background: var(--color-primary);
   columns: var(--color-gray-100);
   font-size: 1.5rem;
   padding: 0.8rem;
   display: grid;
   place-items: center;
   margin-bottom: 1.5rem;
   margin-inline: auto;
   border-radius: 1rem;
   transition: var(--transition);
}

.card:hover span{
   background: var(--color-secondary);
   color: var(--color-gray-600);
}

.card small{
   margin-top: 1rem;
   display: block;
   font-weight: 300;
}


/* ---------Header component-------- */

.header{
   margin-top: 4rem;
   height: 20rem;
   overflow: hidden;
   border-bottom: 2px solid var(--color-gray-400);
}

.header__container{
   width: 100%;
   height: 100%;
   position: relative;
   display: grid;
   place-items: center;
   background: black;
}

.header__container-bg{
   position: absolute;
   width: 100%;
}

.header__container-bg img{
   opacity: 0.6;
}

.header__content{
   position: relative;
   width: 44%;
   margin: 0 auto;
   text-align: center;
   color: var(--color-gray-100);
}

.header__content h2{
   margin-bottom: 1.5rem;
}

.header__content p{
   color: rgba(255, 255, 255, 0.75);
}

/* -------Media Queries---------- */

/* MEDIA QUERIES (medium screens) */
@media screen and (max-width: 1024px) {
   .container{
      width: var(--container-width-md);
   }
   
   h1{
      font-size: 2rem;
   }
   
   h2{
      font-size: 1.5rem;
   }

   /* Header */
   .header__content{
      width: 76%;
   }
   
}


/* MEDIA QUERIES (small screens) */
@media screen and (max-width: 1024px) {
   h1{
      font-size: 2.2rem;
      line-height: 1.3;
   }

   /* Header */
   section{
      margin-top: 7rem;
   }

   .header{
      height: fit-content;
   }

   .header__content{
      width: var(--container-width-md);
      padding: 3rem 0;
   }

   .header__content p{
      font-size: 0.8rem;
   }
}
