nav{
   height: 4rem;
   width: 100vw;
   background: var(--color-primary);
   display: grid;
   place-items: center;
   position: fixed;
   top: 0;
   left: 0;
   z-index: 99;
}

.nav__toggle-btn {
   display: none;
}

.nav__container {
   height: 100%;
   display: flex;
   justify-content: space-between;
   align-items: center;
   position: relative;
}

.logo {
   width: 8rem;
   display: block;
}

.nav__links {
   display: flex;
   gap: 3rem;
   align-items: center;
}

.nav__links a{
   transition: var(--transition);
}

.nav__links a:hover{
   color: var(--color-secondary);
}

.active-nav{
   position: relative;
}

.active-nav::after {
   content: '';
   display: block;
   width: 1.1rem;
   height: 1.1rem;
   background: var(--color-primary);
   position: absolute;
   left: calc(50% - 0.6rem);
   transform: rotate(45deg);
   margin-top: 0.5rem;
}



/* MEDIA QUERIES (MEDIUM AND SMALL DEVICES) */

@media screen and (max-width: 1024px) {
   .nav__toggle-btn{
      display: inline-block;
      background: transparent;
      font-size: 1.7rem;
      cursor: pointer;
   }

   .nav__toggle-btn svg{
      color: var(--color-gray-100);
   }

   .nav__links{
      position: absolute;
      top: 100%;
      right: 0;
      flex-direction: column;
      gap: 0;
      perspective: 400px;
   }

   .active-nav , .active-nav::after{
      display: none;
   }

   .nav__links li{
      height: 4rem;
      width: 100%;
      box-shadow: -2rem 2rem 5rem rgba(0,0,0,0.4);
   }

   .nav__links li a{
        background-color: var(--color-primary);
        height: 100%;
        width: 100%;
        display: flex;
        align-items: center;
        padding: 1rem 5rem 1rem 3rem;
   }

   .show__nav{
      display: flex;
   }

   .hide__nav{
      display: none;
   }
}