.trainers__container {
   display: grid;
   grid-template-columns: repeat(3, 1fr);
   gap: 4rem;
}

.card.trainer {
   padding: 1rem;
}

.trainer__img {
   border-radius: 0 2rem;
   overflow: hidden;
}

.trainer h3{
   margin-top: 2rem;
}

.trainer p {
   margin-top: 0.5rem;
} 

.trainers__socials {
   display: flex;
   gap: 1rem;
   justify-content: center;
   align-items: center;
   margin: 2rem 0;
}

.trainers__socials a{
   font-size: 0.8rem;
   background: var(--color-gray-600);
   padding: 0.7rem;
   border-radius: 0.7rem;
   transition: var(--transition);
}

.trainer:hover a{
   background: var(--color-gray-500);
}


/* Media Queries (medium screens) */

/* MEDIA QUERIES (medium screens) */
@media screen and (max-width: 1024px) {
   .trainers__container {
      grid-template-columns: 1fr 1fr;
      gap: 2.5rem;
   }
}

/* MEDIA QUERIES (medium screens) */
@media screen and (max-width: 1024px) {
   .trainers__container {
      grid-template-columns: 1fr;
      gap: 2rem;
   }
   
   .card.trainer {
      width: 90%;
      margin-inline: auto;
   }
}

